import { addDecorator, configure, setAddon } from '@storybook/react'
import {withThemesProvider} from 'storybook-addon-styled-component-theme'
import { themes } from '@storybook/components';
import { setOptions } from '@storybook/addon-options'
import { withInfo } from '@storybook/addon-info'
import { withBackgrounds } from '@storybook/addon-backgrounds'
import { dark, light } from 'components/UI/theme'

// Info addon.
addDecorator(withInfo)

// Backgrounds addon.
addDecorator(
  withBackgrounds([
    { name: 'dark', value: dark.colors.darkestBackground, default: true },
    { name: 'light', value: light.colors.darkestBackground },
  ])
);

// Option defaults:
setOptions({
  name: 'Zap Desktop',
  url: 'https://github.com/LN-Zap/zap-desktop',
  theme: themes.dark,
});

// Zap Themes.
const zapThemes = [dark, light]
addDecorator(withThemesProvider(zapThemes));

// automatically import all files ending in *.stories.js
const req = require.context('../stories', true, /.stories.js$/);
function loadStories() {
  req.keys().forEach(filename => req(filename));
}

configure(loadStories, module);
